  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 12px;
  color: #212121;
}

/* skip */

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #bf1722;
  color: white;
  padding: 8px;
  z-index: 100;
}
 
.skip-link:focus {
   top: 0;
}

/* loading */

.hidden {
  display: none;
}

.loading {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 1.5em;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 1em;
  border-radius: 10px;
}

/* header */

.header {
  min-height: 56px;
  transition: min-height 0.3s;
}

.app-bar__brand {
  width: 100%;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.app-bar__brand .logo {
  font-weight: 700;
  font-size: 4em;
  margin: 0.5em 0.25em;
  display: inline-block;
  color: #212121;
}

.app-bar .app-bar__menu button {
  font-size: 32px;
  margin: 10px auto;
  display: block;
  min-width: 44px;
  min-height: 44px;
  border: none;
  background: none;
}

.app-bar .app-bar__navigation {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.nav__list {
  width: 100%;
  padding: 0;
  margin: 0;
}

.nav__item {
  box-sizing: border-box;
  display: inline-block;
  width: 24%;
  text-align: center;
  line-height: 24px;
  text-transform: uppercase;
}

.app-bar__navigation a {
  display: inline-block;
  padding: 1.3em;
  text-decoration: none;
  color: #212121;
  font-size: 15px;
  font-weight:700;
}

.app-bar .app-bar__navigation a:hover {
  text-decoration: underline;
  color: #616161;
}

/* main */
main {
  width: 100%;
  margin: 0 auto;
}

.content .content__heading {
  font-weight: bold;
  margin-top: 40px;
  text-align: center;
  font-size: 24px;
}

.restaurants {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 20px;
  padding: 0 20px;
  margin: 32px auto auto;
}

.restaurant-item {
  padding: 16px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 20px;
  padding: 0;
}

.restaurant-item__image {
  width: 100%;
  height: 300px;
  object-fit: cover;
  margin-bottom: 10px;
  padding: 0 10px;
  border-radius: 15px;
}

.restaurant-item__content h3,
.restaurant-item__content p {
  margin-bottom: 5px;
}

.restaurant-item__content h3 {
  font-size: 16px;
  font-weight: bold;
}

.restaurant-item__content p {
  font-size: 12px;
  padding: 0 10px;
}

.restaurant-item__content a {
  color: #FF0000;
  text-decoration: none;
  padding: 10px 44px 44px 10px;
}

.restaurant-item__content a:hover {
  text-decoration: underline;
}

/* hero */
/* .hero {
  background-image: url("/src/public/images/heros/hero-image_4.jpg");
