.hero {
  height: 50vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.hero .kata {
  background-color: rgba(0, 0, 0, 0.5);
  padding: 20px;
  border-radius: 20px;
}

.hero h1 {
  margin-bottom: 10px;
  font-size: 1.4rem;
}

.hero p {
  font-size: 1.2rem;
}

/* detail */

.restaurant-detail {
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  background-color: #fff;
  margin: 20px auto;
  max-width: 800px;
}

.restaurant__poster {
  width: 100%;
  height: auto;
  border-radius: 8px;
  margin-bottom: 20px;
}

.restaurant-detail .restaurant__name {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 10px;
}

.restaurant__info h4 {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 10px;
}

.restaurant__info p {
  font-size: 16px;
  margin-bottom: 10px;
}

.restaurant__descriptionkedua h3 {
  font-size: 24px;
  font-weight: 600;
  margin-top: 20px;
  margin-bottom: 10px;
}

.restaurant__info button {
  display: inline-block;
  background-color: #FF0000;
  color: white;
  font-size: 14px;
  min-width: 44px;
  min-height: 44px;
  padding: 12px 12px;
  margin-top: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  text-align: center;
}

.restaurant__info button:hover {
  background-color: #AF0404;
}

.restaurant__menu {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.restaurant__menu > div {
  width: 100%;
}

.restaurant__menu h4 {
  font-size: 20px;
  margin-bottom: 5px;
}

.restaurant__menu ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.restaurant__menu li {
  font-size: 16px;
  margin-bottom: 5px;
}

.reviews__item {
  margin-top: 20px;
}

.review {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin-bottom: 10px;
}

.review p {
  margin: 5px 0;
  font-size: 14px;
  color: #212121;
}

.review p strong {
  color: #212121;
}

form#reviewForm {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

form#reviewForm label {
  margin-bottom: 10px;
  font-weight: bold;
  font-size: 16px;
}

form#reviewForm input,
form#reviewForm textarea {
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  min-width: 44px;
  min-height: 44px;
}

form#reviewForm button {
  padding: 10px 20px;
  font-size: 14px;
  font-weight: bold;
  color: #ffffff;
  background-color: #FF0000;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.3s ease;
  min-width: 44px;
  min-height: 44px;
}

form#reviewForm button:hover {
  background-color: #AF0404;
}

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #FF0000;
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  font-size: 18px;
  color: white;
  cursor: pointer;
}


/* footer */

.footer {
  background-color: #212121;
  color: #fff;
  padding: 2em;
  text-align: center;
  width: 100%;
}

.footer__copyright {
  font-size: 14px;
}

@media screen and (max-width: 499px) {
  .app-bar__navigation {
      z-index: 10;
      background-color: #fff;
      width: 300px;
      position: absolute;

      /* This trasform moves the drawer off canvas. */
      -webkit-transform: translate(-300px, 0);
      transform: translate(-600px, 0);

      /* Optionally, we animate the drawer. */
      transition: transform 0.3s ease;
  }

  #navigationDrawer{
    width: max-content;
  }

  .open {
      -webkit-transform: translate(0, 0);
      transform: translate(0, 0);
  }

  .nav__item {
      display: list-item;
      width: 100%;
      text-align: left;
  }
}

@media screen and (min-width: 500px) {
  .app-bar .app-bar__menu button {
      display: none;
  }
}

@media screen and (max-width: 650px) {
  .hero {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  }
}

@media screen and (min-width: 650px) {
  .hero {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  }

  .restaurant-item {
    padding: 16px 32px 32px 32px;
  }

  .restaurant-item__content h3 {
    font-size: 18px;
  }

  .restaurant-item__content p {
    font-size: 14px;
  }
}

@media screen and (min-width: 700px) {
  .restaurants {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 16px;
  }
}

@media screen and (min-width: 800px) {
  .nav__list {
      max-width: 800px;
      margin: 0 auto;
  }
}

@media screen and (min-width: 900px) {
  .restaurants {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
