(auto-fill, minmax(300px, 1fr));\n  grid-gap: 20px;\n  padding: 0 20px;\n  margin: 32px auto auto;\n}\n\n.restaurant-item {\n  padding: 16px;\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n  width: 100%;\n  border-radius: 5px;\n  overflow: hidden;\n  margin-bottom: 20px;\n  padding: 0;\n}\n\n.restaurant-item__image {\n  width: 100%;\n  height: 300px;\n  object-fit: cover;\n  margin-bottom: 10px;\n  padding: 0 10px;\n  border-radius: 15px;\n}\n\n.restaurant-item__content h3,\n.restaurant-item__content p {\n  margin-bottom: 5px;\n}\n\n.restaurant-item__content h3 {\n  font-size: 16px;\n  font-weight: bold;\n}\n\n.restaurant-item__content p {\n  font-size: 12px;\n  padding: 0 10px;\n}\n\n.restaurant-item__content a {\n  color: #FF0000;\n  text-decoration: none;\n  padding: 10px 44px 44px 10px;\n}\n\n.restaurant-item__content a:hover {\n  text-decoration: underline;\n}\n\n/* hero */\n/* .hero {\n  background-image: url(\"/src/public/images/heros/hero-image_4.jpg\");\n  background-size: cover;\n  background-position: center;\n  height: 50vh;\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  text-align: center;\n  color: white;\n  padding: 0;\n  margin: 0;\n} */\n\n.hero {\n  height: 50vh;\n  background-size: cover;\n  background-position: center;\n  background-repeat: no-repeat;\n  color: white;\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  text-align: center;\n}\n\n.hero .kata {\n  background-color: rgba(0, 0, 0, 0.5);\n  padding: 20px;\n  border-radius: 20px;\n}\n\n.hero h1 {\n  margin-bottom: 10px;\n  font-size: 1.4rem;\n}\n\n.hero p {\n  font-size: 1.2rem;\n}\n\n/* detail */\n\n.restaurant-detail {\n  padding: 20px;\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);\n  border-radius: 8px;\n  background-color: #fff;\n  margin: 20px auto;\n  max-width: 800px;\n}\n\n.restaurant__poster {\n  width: 100%;\n  height: auto;\n  border-radius: 8px;\n  margin-bottom: 20px;\n}\n\n.restaurant-detail .restaurant__name {\n  font-size: 24px;\n  font-weight: 700;\n  margin-bottom: 10px;\n}\n\n.restaurant__info h4 {\n  font-size: 16px;\n  font-weight: bold;\n  margin-bottom: 10px;\n}\n\n.restaurant__info p {\n  font-size: 16px;\n  margin-bottom: 10px;\n}\n\n.restaurant__descriptionkedua h3 {\n  font-size: 24px;\n  font-weight: 600;\n  margin-top: 20px;\n  margin-bottom: 10px;\n}\n\n.restaurant__info button {\n  display: inline-block;\n  background-color: #FF0000;\n  color: white;\n  font-size: 14px;\n  min-width: 44px;\n  min-height: 44px;\n  padding: 12px 12px;\n  margin-top: 10px;\n  border: none;\n  border-radius: 5px;\n  cursor: pointer;\n  font-weight: bold;\n  text-align: center;\n}\n\n.restaurant__info button:hover {\n  background-color: #AF0404;\n}\n\n.restaurant__menu {\n  display: flex;\n  justify-content: space-between;\n  margin-bottom: 20px;\n}\n\n.restaurant__menu > div {\n  width: 100%;\n}\n\n.restaurant__menu h4 {\n  font-size: 20px;\n  margin-bottom: 5px;\n}\n\n.restaurant__menu ul {\n  list-style-type: none;\n  padding: 0;\n  margin: 0;\n}\n\n.restaurant__menu li {\n  font-size: 16px;\n  margin-bottom: 5px;\n}\n\n.reviews__item {\n  margin-top: 20px;\n}\n\n.review {\n  padding: 10px;\n  border: 1px solid #ddd;\n  border-radius: 8px;\n  margin-bottom: 10px;\n}\n\n.review p {\n  margin: 5px 0;\n  font-size: 14px;\n  color: #212121;\n}\n\n.review p strong {\n  color: #212121;\n}\n\nform#reviewForm {\n  margin-top: 20px;\n  display: flex;\n  flex-direction: column;\n}\n\nform#reviewForm label {\n  margin-bottom: 10px;\n  font-weight: bold;\n  font-size: 16px;\n}\n\nform#reviewForm input,\nform#reviewForm textarea {\n  margin-bottom: 15px;\n  padding: 10px;\n  border: 1px solid #ddd;\n  border-radius: 5px;\n  font-size: 16px;\n  min-width: 44px;\n  min-height: 44px;\n}\n\nform#reviewForm button {\n  padding: 10px 20px;\n  font-size: 14px;\n  font-weight: bold;\n  color: #ffffff;\n  background-color: #FF0000;\n  border: none;\n  border-radius: 5px;\n  cursor: pointer;\n  text-align: center;\n  text-decoration: none;\n  transition: background-color 0.3s ease;\n  min-width: 44px;\n  min-height: 44px;\n}\n\nform#reviewForm button:hover {\n  background-color: #AF0404;\n}\n\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: #FF0000;\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n\n\n/* footer */\n\n.footer {\n  background-color: #212121;\n  color: #fff;\n  padding: 2em;\n  text-align: center;\n  width: 100%;\n}\n\n.footer__copyright {\n  font-size: 14px;\n}\n\n@media screen and (max-width: 499px) {\n  .app-bar__navigation {\n      z-index: 10;\n      background-color: #fff;\n      width: 300px;\n      position: absolute;\n\n      /* This trasform moves the drawer off canvas. */\n      -webkit-transform: translate(-300px, 0);\n      transform: translate(-600px, 0);\n\n      /* Optionally, we animate the drawer. */\n      transition: transform 0.3s ease;\n  }\n\n  #navigationDrawer{\n    width: max-content;\n  }\n\n  .open {\n      -webkit-transform: translate(0, 0);\n      transform: translate(0, 0);\n  }\n\n  .nav__item {\n      display: list-item;\n      width: 100%;\n      text-align: left;\n  }\n}\n\n@media screen and (min-width: 500px) {\n  .app-bar .app-bar__menu button {\n      display: none;\n  }\n}\n\n@media screen and (max-width: 650px) {\n  .hero {\n    background-image: url(\"/dist/images/heros/hero-image_4-small.jpg\");\n  }\n}\n\n@media screen and (min-width: 650px) {\n  .hero {\n    background-image: url(\"/dist/images/heros/hero-image_4-large.jpg\");\n  }\n\n  .restaurant-item {\n    padding: 16px 32px 32px 32px;\n  }\n\n  .restaurant-item__content h3 {\n    font-size: 18px;\n  }\n\n  .restaurant-item__content p {\n    font-size: 14px;\n  }\n}\n\n@media screen and (min-width: 700px) {\n  .restaurants {\n    display: grid;\n    grid-template-columns: 1fr 1fr;\n    grid-column-gap: 10px;\n    grid-row-gap: 16px;\n  }\n}\n\n@media screen and (min-width: 800px) {\n  .nav__list {\n      max-width: 800px;\n      margin: 0 auto;\n  }\n}\n\n@media screen and (min-width: 900px) {\n  .restaurants {\n    display: grid;\n    grid-template-columns: 1fr 1fr 1fr;\n  }\n}"],"sourceRoot":""}]);
